import React, { useState, useEffect } from "react";
import { signInWithEmailAndPassword } from "firebase/auth";
import { useNavigate } from "react-router-dom";
import { useAuthState } from "react-firebase-hooks/auth";

import { auth } from "../firebase-config";

export const Login = () => {
  const [email, setEmail] = useState(null);
  const [password, setPassword] = useState(null);
  const [loading] = useAuthState(auth);
  const navigate = useNavigate();

  useEffect(() => {
    const userAccessToken = sessionStorage.getItem("accessToken");
    if (userAccessToken) navigate("/admin-space", { replace: true });
  }, []);

  const logInWithEmailAndPassword = async (email, password) => {
    try {
      await signInWithEmailAndPassword(auth, email, password).then((userCredential) => {
        sessionStorage.setItem('accessToken', userCredential.user.accessToken);
        navigate("/admin-space", { replace: true });
      })
        .catch((error) => {
          const errorCode = error.code;
          const errorMessage = error.message;
          alert(`${errorCode} - ${errorMessage}`);
        })
    } catch (err) {
      console.error(err);
      alert(err.message);
    }
  };

  const RenderLoginBox = () => (
    <div className="w-25 login-box">
      <label for="email">Email:</label>
      <input
        type="email"
        id="email"
        onChange={(evt) => setEmail(evt.target.value)}
        className="login-input"
      />
      <label for="password">Password:</label>
      <input
        type="password"
        id="password"
        onChange={(evt) => setPassword(evt.target.value)}
        className="login-input"
      />
      <button
        type="submit"
        onClick={() => {
          if (email && password) {
            logInWithEmailAndPassword(email, password)
          }
        }}
        className="login-btn"
      >
        Login
      </button>
    </div>
  );

  return (
    <div className="login-container">
      <RenderLoginBox />
    </div>
  );
};
