import React from "react";
import ReactMde from "react-mde";
import ReactMarkdown from "react-markdown";
import "react-mde/lib/styles/css/react-mde-all.css";

export const MarkdownEditor = ({ handleEditorValue, value, setValue }) => {
    const [selectedTab, setSelectedTab] = React.useState("write");

    const handleOnChange = (editorValue) => {
        handleEditorValue(editorValue, setValue);
        setValue(editorValue);
    }

    return (
        <div className="description-input">
            <ReactMde
                value={value}
                onChange={handleOnChange}
                selectedTab={selectedTab}
                onTabChange={setSelectedTab}
                generateMarkdownPreview={(markdown) => Promise.resolve(<ReactMarkdown children={markdown} />)}
            />
        </div>
    );
};
