import { getFirestore, collection, getDocs } from "firebase/firestore";

import React, { useState, useEffect } from "react";
import { Card } from '../components/card';

const db = getFirestore();

export const LandingPage = () => {
  const [blogData, setBlogData] = useState([]);
  const [isloading, setIsloading] = useState(false);
  const titlesData = []

  const getData = async () => {
    setIsloading(true)
    const querySnapshot = await getDocs(collection(db, "blog-data"));
    querySnapshot.docs.forEach((blog) => {
      titlesData.push(blog.data().title)
    })
    setBlogData(titlesData)
    setIsloading(false)
  };

  useEffect(() => {
    getData();
  }, [])

  const RenderCard = () => {
    return Array.isArray(blogData) && blogData.map((title) => <Card title={title} />)
  }

  return (
    <>
      <header className="header">
        <div className="d-flex m-30">
          <span className="ml-30 logo">Akshat Soni</span>
        </div>
      </header>
      <div className="landing-container">
        {isloading ? <div className="loader"></div> : <RenderCard />}
      </div>
    </>
  );
};
