import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";

import { addDoc, collection } from "firebase/firestore";
import { logout, db } from "../firebase-config";
import { MarkdownEditor } from "./react-markdown-editor";

export const AdminSpace = () => {
  const navigate = useNavigate();
  const [value, setValue] = React.useState('');
  const [editorValue, setEditorValue] = React.useState("");
  const [textValue, setTextValue] = React.useState("");

  useEffect(() => {
    const userAccessToken = sessionStorage.getItem("accessToken");
    if (!userAccessToken) {
      navigate("/login");
    }
  }, []);

  const userLogout = async () => {
    await logout();
    const userAccessToken = sessionStorage.getItem("accessToken");
    if (!userAccessToken) {
      navigate("/login");
    }
  };

  const handleTitleValue = (e) => setTextValue(e?.target?.value);

  const handleEditorValue = (value) => setEditorValue(value);

  const handleSubmit = async () => {
    if (editorValue && editorValue) {
      const docRef = await addDoc(collection(db, "blog-data"), {
        title: textValue,
        description: editorValue,
      });
      if (docRef.id) {
        setValue("");
        setTextValue("");
      }
    }
  };

  return (
    <>
      <header>
        <button className="login-btn" onClick={() => userLogout()}>
          logout
        </button>
      </header>
      <div className="editor-container">
        <div>
          <label htmlFor="blog-title">Title</label>
          <br />
          <input
            id="blog-title"
            type="text"
            className="title-input"
            value={textValue}
            onChange={handleTitleValue}
          />
        </div>
        <div>
          <label htmlFor="blog-description">Description</label>
          <br />
          <MarkdownEditor
            id="blog-description"
            value={value}
            setValue={setValue}
            handleEditorValue={handleEditorValue}
          />
        </div>
        <button onClick={handleSubmit} className="login-btn">
          SAVE
        </button>
      </div>
    </>
  );
};
